import React, { useState, useCallback, useRef, useEffect } from "react";

import "./HelpPage.css";

import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import ImageLabel from "../../components/Labels/ImageLabel/ImageLabel.js";
import Switch from "../../components/Switch/Switch.js";

import VersionData from "../../components/Versions/VersionData.js";
import getOperatingSystem from "../../utils/os.js";

import logoImage from '../../img/logo.png';

import { default as HelpContentWindows } from "./windows/HelpContent.js";
import { default as HelpContentMacOS } from "./macos/HelpContent.js";


const HelpPage = ({language}) => {
  const [operatingSystemType, setOperatingSystemType] = useState(getOperatingSystem().toLowerCase());
  const [showOperatingSystemOptions, setShowOperatingSystemOptions] = useState(false);
  const dropdownRef = useRef(null);
  const [textOnlyEnabled, setTextOnlyEnabled] = useState(false);

  useEffect(() => {
    const handleClick = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setShowOperatingSystemOptions(false);
      }
    };

    document.addEventListener("mousedown", handleClick);

    return () => {
      document.removeEventListener("mousedown", handleClick);
    };
  }, []);

  const changeOperatingSystemType = useCallback((osType) => {
    setOperatingSystemType(osType);
    const checkUrl = new URL(window.location.href);
    checkUrl.searchParams.set("type", osType);
    window.history.replaceState(null, null, checkUrl.href);
  }, []);

  useEffect(() => {
    const checkUrl = new URL(window.location.href);
    const osType = (checkUrl.searchParams.get("type") || "").toLowerCase();
    if (["windows", "macos"].includes(osType)) {
      changeOperatingSystemType(osType);
    }
    else {
      const newOsType = getOperatingSystem().toLowerCase();
      changeOperatingSystemType(newOsType);
      checkUrl.searchParams.set("type", newOsType);
      window.history.replaceState(null, null, checkUrl.href);
    }
  }, [changeOperatingSystemType]);

  const T = useCallback((en, ko) => {
    if (language === "en") {
      return en;
    }
    else {
      return ko;
    }
  }, [language]);

  const getCurrentOperatingSystemDisplayName = useCallback(() => {
    return {
      windows: "Windows",
      macos: "macOS"
    }[operatingSystemType];
  }, [operatingSystemType]);

  return (
    <div className="HelpPage">
      <PageFrame>
        <Content.TitleBar>{T("Manual & Help", "사용법 & 도움말")}</Content.TitleBar>
        <Content.Area className="helpContentArea" style={{padding: "30px"}}>
          <ImageLabel style={{padding: "15px", width: "200px", height: "200px"}} src={logoImage}/>
          <div className="infoArea" style={{marginLeft: "40px", width: "100%"}}>
            <div style={{display: "flex"}}>
              <Label style={{fontSize: "35pt"}}>TwitchLink</Label>
              <Label style={{marginTop: "30px", fontSize: "15pt", color: "gray"}}>{VersionData.latestVersion()}</Label>
            </div>
            <Label style={{fontSize: "15pt", color: "gray"}}>{T("Getting Started", "도움말")}</Label>
            <div style={{position: "relative", display: "flex", marginTop: "40px", justifyContent: "flex-end"}}>
              <div className="Button osTypeDropdownButton" onClick={() => setShowOperatingSystemOptions(true)}>
                {
                  operatingSystemType === "windows"
                  ?
                  <svg style={{width: "32px", height: "32px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" fill="currentColor">
                    <path d="M31.87 30.58H244.7v212.81H31.87zM266.89 30.58H479.7v212.81H266.89zM31.87 265.61H244.7v212.8H31.87zM266.89 265.61H479.7v212.8H266.89z"/>
                  </svg>
                  :
                  <svg style={{width: "32px", height: "32px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
                    <path d="M12.152 6.896c-0.948 0-2.415-1.078-3.96-1.04-2.04 0.027-3.91 1.183-4.961 3.014-2.117 3.675-0.546 9.103 1.519 12.09 1.013 1.454 2.208 3.09 3.792 3.039 1.52-0.065 2.09-0.987 3.935-0.987 1.831 0 2.35 0.987 3.96 0.948 1.637-0.026 2.676-1.48 3.676-2.948 1.156-1.688 1.636-3.325 1.662-3.415-0.039-0.013-3.182-1.221-3.22-4.857-0.026-3.04 2.48-4.494 2.597-4.559-1.429-2.09-3.623-2.324-4.39-2.376-2-0.156-3.675 1.09-4.61 1.091zM15.53 3.83c0.843-1.012 1.4-2.427 1.245-3.83-1.207 0.052-2.662 0.805-3.532 1.818-0.78 0.896-1.454 2.338-1.273 3.714 1.338 0.104 2.715-0.688 3.559-1.701"></path>
                  </svg>
                }
                {T(`Help for ${getCurrentOperatingSystemDisplayName()}`, `${getCurrentOperatingSystemDisplayName()}용 도움말`)}
                <svg style={{width: "32px", height: "32px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                  <path d="m6 9 6 6 6-6"/>
                </svg>
              </div>
              {
                showOperatingSystemOptions
                ?
                <div ref={dropdownRef} className="osTypeDropdown">
                  <div className="osTypeDropdownOption" onClick={() => {
                    changeOperatingSystemType("windows");
                    setShowOperatingSystemOptions(false);
                  }}>
                    <svg style={{width: "16px", height: "16px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" fill="currentColor">
                      <path d="M31.87 30.58H244.7v212.81H31.87zM266.89 30.58H479.7v212.81H266.89zM31.87 265.61H244.7v212.8H31.87zM266.89 265.61H479.7v212.8H266.89z"/>
                    </svg>
                    {T("Help for Windows", "Windows용 도움말")}
                  </div>
                  <div className="osTypeDropdownOption" onClick={() => {
                    changeOperatingSystemType("macos");
                    setShowOperatingSystemOptions(false);
                  }}>
                    <svg style={{width: "16px", height: "16px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
                      <path d="M12.152 6.896c-0.948 0-2.415-1.078-3.96-1.04-2.04 0.027-3.91 1.183-4.961 3.014-2.117 3.675-0.546 9.103 1.519 12.09 1.013 1.454 2.208 3.09 3.792 3.039 1.52-0.065 2.09-0.987 3.935-0.987 1.831 0 2.35 0.987 3.96 0.948 1.637-0.026 2.676-1.48 3.676-2.948 1.156-1.688 1.636-3.325 1.662-3.415-0.039-0.013-3.182-1.221-3.22-4.857-0.026-3.04 2.48-4.494 2.597-4.559-1.429-2.09-3.623-2.324-4.39-2.376-2-0.156-3.675 1.09-4.61 1.091zM15.53 3.83c0.843-1.012 1.4-2.427 1.245-3.83-1.207 0.052-2.662 0.805-3.532 1.818-0.78 0.896-1.454 2.338-1.273 3.714 1.338 0.104 2.715-0.688 3.559-1.701"></path>
                    </svg>
                    {T("Help for macOS", "macOS용 도움말")}
                  </div>
                </div>
                :
                null
              }
            </div>
          </div>
        </Content.Area>
        {
          operatingSystemType === "windows"
          ?
          <HelpContentWindows language={language} textOnly={textOnlyEnabled}/>
          :
          <HelpContentMacOS language={language} textOnly={textOnlyEnabled}/>
        }
      </PageFrame>
      <div className="imageToggler">
        <div style={{margin: "5px 10px"}}>
          {T("Text Only", "텍스트만 표시")}
        </div>
        <Switch isChecked={textOnlyEnabled} handleToggle={setTextOnlyEnabled}/>
      </div>
    </div>
  );
};

export default HelpPage;