import React from "react";

import "./Switch.css";

const Switch = (props) => {
  let { className, isChecked, handleToggle, ...rest } = props;
  if (className === undefined) {
    className = "Switch";
  }
  else {
    className += " Switch";
  }
  let labelClassName = isChecked ? "switch-label label-switched" : "switch-label";
  let ballClassName = isChecked ? "switch-ball ball-switched" : "switch-ball";
  return (
    <div className={className} {...rest}>
      <input type="checkbox" className={"switch-checkbox"} checked={isChecked} onChange={(event) => handleToggle(event.target.checked)} id={"switch-input"}/>
      <label className={labelClassName} htmlFor={"switch-input"}>
        <div className={ballClassName}></div>
      </label>
    </div>
  );
};

export default Switch;